
import { defineComponent, inject, PropType } from "vue";
import { IValid } from "@/types/data";

export default defineComponent({
  name: "InputApp",
  inheritAttrs: false,
  data() {
    const form: HTMLFormElement | undefined = inject("form");
    return {
      isFirstInput: true,
      isValid: true,
      error: "",
      form,
    };
  },
  props: {
    value: {
      type: String as PropType<string>,
      default: "",
    },
    messageError: {
      type: String as PropType<string>,
      default: "",
    },
    validationRules: {
      type: Array as PropType<((str: string) => IValid)[]>,
      default: () => [],
    },
  },
  watch: {
    value(): void {
      if (this.isFirstInput) return;
      this.validate();
    },
  },
  methods: {
    blurHandler(): void {
      if (this.isFirstInput) {
        this.validate();
      }
      this.isFirstInput = false;
    },
    validate(): boolean {
      this.isValid = this.validationRules.every(rule => {
        const { result, message } = rule(this.value);
        if (!result) {
          this.error = message || this.messageError;
        }
        return result;
      });
      return this.isValid;
    },
    reset() {
      this.$emit("update:value", "");
      this.isFirstInput = true;
      this.isValid = true;
    },
  },
  mounted() {
    if (this.form) {
      this.form.registerInput(this);
    }
  },
  unmounted() {
    if (this.form) {
      this.form.unRegisterInput(this);
    }
  },
});
